import { Component, ElementRef, OnInit, ViewChild, Renderer2,AfterViewInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { DOCUMENT } from '@angular/common';
import { Inject } from '@angular/core';
import { IonAlertService } from '../services/ion-alert.service';
import { LoadingController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { ModalMintComponent } from '../componentes/modal-mint/modal-mint.component';
import JSONContract from '../thewall.json';
import { NFTStorage } from 'nft.storage';

// web3 librerias
import Web3 from 'web3';

@Component({
  selector: 'app-mintear',
  templateUrl: './mintear.page.html',
  styleUrls: ['./mintear.page.scss'],
})
export class MintearPage implements OnInit,AfterViewInit {

  web3: any;
  //colores predefinidos iniciales
  colores:string[]=["#451d35","#c82f43","#fea24b","#fffeef","#979e98"];

  //color elegido
  colorElegido: { id: number, color: string }[] = [
    { "id": 0, "color": this.colores[0] },
   ];

   
   // atributos cambas 
   widthP:number=0;   //pixels
   heightP:number=0;   //pixels
   sizeCanvas: number[]=[this.widthP,this.heightP]; // formato para el SC
   widthArray:any[]=[];       //necesario para que el NGfor actualice el DOM
   heightArray:any[]=[];     //necesario para que el NGfor actualice el DOM
   contadorDOMngFor:number=1;  //necesario para que el NGfor actualice el DOM
   pixelSize:number=16;      // para la imagen bitmap
   idpixel:string="divP";     // parte inicial del ID para los DIV generados dinámicamente
   idpixelTarget:string="divPT"; // parte inicial del ID pixel target

   CanvasPixelsArray: number[]=[0];  // esencial para introducir los datos de la imagen 

    // elementos DOM 
     
  @ViewChild('color1') color1: ElementRef;
  @ViewChild('color2') color2: ElementRef;
  @ViewChild('color3') color3: ElementRef;
  @ViewChild('color4') color4: ElementRef;
  @ViewChild('color5') color5: ElementRef;
  @ViewChild('colorSelect') colorSelect: ElementRef;
  @ViewChild('canvasFly') canvasFl: ElementRef;
 
  
  color1Div:any;
  color2Div:any;
  color3Div:any;
  color4Div:any;
  color5Div:any;
  colorSelectDiv:any;
  canvasFly:any;
  FormMint: any;
  address:any;
  chainID:any;
  contractAddress:any;
  contract:any;
  provider:any;
  window:any;
  ipfs:any;
  clienteIPFS:any;
  loading:any;
  NFTnombre:any;
  NFTdescripcion:any;


  constructor(private modalCtrl: ModalController, @Inject(DOCUMENT) private document: Document,private loadingCtrl: LoadingController,private renderer2: Renderer2,private FormBuilder: FormBuilder,private ionicAlertService: IonAlertService) 
  {

    this.contractAddress=localStorage.getItem("contratoTheWall");

    const NFT_STORAGE_TOKEN = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiJkaWQ6ZXRocjoweDhkOGI1ZTFmNjFhRTI5ZWYzQTZFOTgzQ2Y2RWEzNDQyNjk4NWFjNTAiLCJpc3MiOiJuZnQtc3RvcmFnZSIsImlhdCI6MTY2NjY5NDY4ODM4MywibmFtZSI6InRoZXdhbGwifQ.YMQpCds-p48GeLhBaJzQOhWpjheVZUFdL7c0bOvcDIQ';
    this.clienteIPFS = new NFTStorage({ token: NFT_STORAGE_TOKEN });

    this.FormMint=this.FormBuilder.group({
      nombre:'',
      descripcion:''
     })

     this.web3 = new Web3;
     this.web3.setProvider(
     new this.web3.providers.HttpProvider('https://goerli.infura.io/v3/cdb31d0cd0aa40d894216d4e272f6bb4')
     );
     this.contract = new this.web3.eth.Contract(JSONContract.abi, this.contractAddress);
  }

  ngAfterViewInit(): void {
    
  this.canvasFly=this.canvasFl.nativeElement;
  this.color1Div=this.color1.nativeElement;
  this.color2Div=this.color2.nativeElement;
  this.color3Div=this.color3.nativeElement;
  this.color4Div=this.color4.nativeElement;
  this.color5Div=this.color5.nativeElement;
  this.colorSelectDiv=this.colorSelect.nativeElement;
    
   this.renderer2.setStyle(this.color1Div,'background-color',this.colores[0]);
   this.renderer2.setStyle(this.color2Div,'background-color',this.colores[1]);
   this.renderer2.setStyle(this.color3Div,'background-color',this.colores[2]);
   this.renderer2.setStyle(this.color4Div,'background-color',this.colores[3]);
   this.renderer2.setStyle(this.color5Div,'background-color',this.colores[4]);
   this.renderer2.setStyle(this.colorSelectDiv,'background-color',this.colores[0]);

  }

  ngOnInit() {

   this.CambiaResolucion(16,16); // inicializamos el canvas DOM.
   this.CreacionCanvasArray();
   

   var canvas= document.getElementById('contenCanvas'); // evita el arrastre de pixels
   canvas.addEventListener('selectstart', function(e) { e.preventDefault(); return false; }, false);

   this.address=sessionStorage.getItem("address");
   this.chainID=sessionStorage.getItem("chainid");
   this.window = document.defaultView;
   this.provider=this.window.ethereum; // METAMASK
   
  }


  CreacionCanvasArray(){
   // creacion del array de datos del canvas para enviar a la blockchain
   this.CanvasPixelsArray=[0]; 
   var cantidadPixel=this.widthP*this.heightP;
      for(let i = 0 ; i < cantidadPixel-1 ; i++)  
        this.CanvasPixelsArray.push(0);           // el color 0 es el primero, el de fondo.
    
  }

  cambiaResCheck(value){

     if (value.target.value=="16")
      this.CambiaResolucion(16,16);
    
     if (value.target.value=="32")
     this.CambiaResolucion(32,32);
    
     this.CreacionCanvasArray();  //actualizamos el array 
     
  }
   
  CambiaResolucion(x,y)
  {
    this.widthP=x;
    this.heightP=y;  
    this.sizeCanvas=[this.widthP,this.heightP];

    this.widthArray=[];
    this.heightArray=[];

    for (let x=0;x<this.widthP;x+=1)
      this.widthArray.push(this.contadorDOMngFor+1);
    for (let y=0;y<this.heightP;y+=1)
      this.heightArray.push(this.contadorDOMngFor+1);

      this.contadorDOMngFor++;

     
  }

  CambiaColor(idcolor:number) {
    this.colorElegido[0].id=idcolor;
    this.colorElegido[0].color=this.colores[idcolor];
    this.renderer2.setStyle(this.colorSelectDiv,'background-color',this.colores[idcolor]);
  }


  MuestraPixelTarget(idpixel:number,e) {
    var id=this.generaIDTarget(idpixel);
    var id2=document.getElementById(id);
    id2.style.backgroundColor=this.colorElegido[0].color;
    id2.style.display='block';
  
  }

  OcultaPixelTarget(idpixel:number,e) {
    var id=this.generaIDTarget(idpixel);
    var id2=document.getElementById(id);
    id2.style.display='none';
  }


  PintaPixelArrastra(idpixel:number,e) {

    var id=(e.target as HTMLDivElement).id;  // otra manera de obtener el id
    var id2=document.getElementById(id);
        if(e.buttons==1)  // solo si está presionado el boton
        {
        id2.style.backgroundColor=this.colorElegido[0].color;
        this.PintaArray(idpixel);
        }  
   
  }

  PintaPixelClick(idpixel:number,e) {
    var id=this.generaIDPixel(idpixel);
    var id2=document.getElementById(id);
    id2.style.backgroundColor=this.colorElegido[0].color;
    this.PintaArray(idpixel);
  }

  generaIDTarget(id:number) :string{
    var idpix=this.idpixelTarget+ id.toString();
    return idpix;
  }

  generaIDPixel(id:number) :string{
    var idpix=this.idpixel+ id.toString();
    return idpix;
  }

  PintaArray(id:number)
  {
    this.CanvasPixelsArray[id-1]=this.colorElegido[0].id; // modificamos el valor en el array por el ID color
  }

  RellenaCanvasPixels(idcolor:number)
  {
   var cantidadPixel=this.widthP*this.heightP;
   var IdPixel;
    for(let i = 0 ; i < cantidadPixel ; i++)  
    {
        IdPixel=this.idpixel+(i+1).toString();
        var id= document.getElementById(IdPixel);
        id.style.backgroundColor =this.colores[idcolor];
        this.CanvasPixelsArray[i]=idcolor;          // el color 0 es el color de fondo
    }
    
  }

  sendMint(formData:any){

   if (formData.nombre=='')
   {
    this.ionicAlertService.showAlert("¡Falta nombre!","¿No querrás que tu obra quede anónima verdad?");
    return;
    }
  
    if (formData.descripcion=='')
    {
    this.ionicAlertService.showAlert("¡Falta descripción!","Cuéntanos algo sobre tu obra...");
    return;
    }
    
    
    this.NFTnombre=formData.nombre;
    this.NFTdescripcion=formData.descripcion;

     this.Confirmar(formData.nombre,formData.descripcion);

  }

  async Confirmar(nombreNFT:string,descripcionNFT:string)
  {

    // FASE 1: Subir imagen generada al vuelo
    // Generacion imagen png al vuelo, necesaria para opensea y demás marketplaces
    // Paso 1: Transformarmos los colores HEX en formato RGB para pintar el canvas.

    let coloresRGB = new Array(this.colores.length);
    for (let i = 0; i < coloresRGB.length; i++) {
      coloresRGB[i] = new Array(3);
    }
  
    for (let x=0;x<this.colores.length;x+=1)
    {
      coloresRGB[x][0]=this.hexToRgb(this.colores[x]).r; 
      coloresRGB[x][1]=this.hexToRgb(this.colores[x]).g; 
      coloresRGB[x][2]=this.hexToRgb(this.colores[x]).b; 
    }

    // Paso 2: generacion de canvas con ayuda de canvasaux

    this.canvasFly.width=this.widthP*this.pixelSize;
    this.canvasFly.height=this.heightP*this.pixelSize;

    const ctx = this.canvasFly.getContext('2d');
    let imageData = ctx.createImageData(this.pixelSize, this.pixelSize);  // para el png los pixels son más grandes.

    // Por cada pixel de nuestro canvas pintamos pixels de mayor tamaño en el canvas

    let fila=0;
    let columna=0;
    for (let p = 0; p < this.CanvasPixelsArray.length; p += 1) {
      let crgb=0;
      for (let l = 0; l < this.pixelSize*this.pixelSize; l += 1) 
      {
          let idColor=this.CanvasPixelsArray[p];
          imageData.data[crgb + 0] = coloresRGB[idColor][0];  // R value
          imageData.data[crgb + 1] = coloresRGB[idColor][1];   // G value
          imageData.data[crgb + 2] = coloresRGB[idColor][2];   // B value
          imageData.data[crgb + 3] = 255;  // Alpha: siempre 100% visible.
          crgb+=4;
      }

      if (p>0)
      {
        if (p%this.widthP==0)  // cambio de fila y reset de columna
          {
          fila++;
          columna=0;
          }
       }

      let coordenadaX=columna*this.pixelSize;
      let coordenadaY=fila*this.pixelSize;
      ctx.putImageData(imageData, coordenadaX, coordenadaY);

      columna++;
    } 

   const modal= await this.modalCtrl.create({
      component:ModalMintComponent,
      componentProps: {
        nombre: nombreNFT,
        descripcion: descripcionNFT,
        canvas:this.canvasFly
      }
    });

    await modal.present();
    const {data} = await modal.onDidDismiss(); // se detiene el hilo principal hasta que el modal no sea descartado
  
    if (data.orden=="confirmar")
    {
      await this.canvasFly.toBlob((blob) => {    // creamos blob de datos
      this.showLoading();
       this.subirBlobIPFS(blob,1); // 1 significa paso 1. despues de subir la imagen subira el JSON
    });
    }
  } 


  async subirBlobIPFS(datosBlob:any,paso:any)
  {
    await this.clienteIPFS.storeBlob(datosBlob).then((result) => {

      this.loading.dismiss();
      var urlIPFS="https://"+result+".ipfs.nftstorage.link/";

      if(paso==1)  // tipo 1 ha terminado de subir imagen, asi que ahora toca JSON
      this.subirIpfsJson(urlIPFS);

      if(paso==2)  // tipo 2 ha terminado de subir JSON, ahora toca subir SC
      this.publicarContrato(urlIPFS);
        
        }).catch((err)=>{
          this.loading.dismiss();
          this.ionicAlertService.showAlert("¡NFT Storage ERROR!","Los metadatos no han podido ser introducidos en el servidor IPFS");
        }) 
  }


  async subirIpfsJson(urlImagen)
  {
    // JSON para OpenSea y demás NFT Marketplaces

    var jsonText="{\"description\":\""+this.NFTdescripcion+"\",";
    jsonText=jsonText+ "\"image\":\""+urlImagen+"\",";
    jsonText=jsonText+ "\"name\":\""+this.NFTnombre+"\"";
    jsonText=jsonText + "}";

    let blob = new Blob([jsonText], {type: '.json'});

    this.showLoading();
    this.subirBlobIPFS(blob,2); // 1 imagen, 2 json
  }

  async publicarContrato(TokenURI) {

    const transactionParameters = {
      nonce: '0x00', // ignored by MetaMask
     // gasPrice: this.web3.utils.toHex(10000000000000), // customizable by user during MetaMask confirmation.
     // gas: this.web3.utils.toHex(1000000), // customizable by user during MetaMask confirmation. GAS LIMIT
      to: this.contractAddress, // Required except during contract publications.
      from: this.address, // must match user's active address.
      value: '0', // Only required to send ether from exteral. Si no tiene requisito payable se deja a cero.
      data:this.contract.methods.mint(this.address,TokenURI,this.NFTnombre,this.CanvasPixelsArray,this.sizeCanvas,this.colores).encodeABI(),
      chainId: this.chainID, // Used to prevent transaction reuse across blockchains. Auto-filled by MetaMask.
    };

   const txHash = await this.provider.request({
    method: 'eth_sendTransaction',
    params: [transactionParameters],
     }).then((result) => {
      this.ionicAlertService.showAlert("MetaMask está procesando la solicitud","Cuando haya terminado recibirás una confirmacíon en Metamask");
    }).catch((err)=>{
        if (err.code == 4001)
        this.ionicAlertService.showAlert("MetaMask","La transacción ha sido cancelada");
     })
     
   }


   hexToRgb(hex)
   {
     var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
     return result ? {
       r: parseInt(result[1], 16),
       g: parseInt(result[2], 16),
       b: parseInt(result[3], 16)
     } : null;
   }

   async showLoading() {
    this.loading = await this.loadingCtrl.create({
      message: 'Creando NFT...',
    });
    this.loading.present();
  }


}
